import React from 'react';
import { useParams } from 'react-router-dom';
import Heroes from '../componentBoot/heroes';
import { useSelector } from 'react-redux';
const Ditailes = (props) => {
  const {id}=useParams()
  const {etate}=props;
  const products = useSelector((state)=>state.produis)
  const product=products.find(product=>product.id==parseInt(id))
  return (
    <Heroes product={product} etate={etate}/>
  );
}

export default Ditailes;
