
import { produis } from "./dataProduit"
const insialleState=produis

export  const reducerProduit=(state=insialleState,action)=> {
switch(action.type){
case 'ADD_PRODUIT':
  return {...state, produis:[action.payload,...state.produis]}
case 'UPDATE_PRODUIT':
  return {...state, produis:[ action.payload,...state.produis.filter(produit=>parseInt(produit.id) !==  parseInt(action.payload.id)) ]}
case 'DELETE_PRODUIT': 
return {...state, produis:[ ...state.produis.filter(produit=>produit.id != action.payload)]}

case 'SIGN_UP':
  return {...state,users:[...state.users,action.payload]}

case 'ADD_CART_CLIENT' : 
  return {...state,carts:[action.payload,...state.carts]}

  case 'PLUS_QUNT_PROD' :
    return {...state,carts:[action.payload,...state.carts.filter(catr=>parseInt(catr.id) !== parseInt(action.payload.id))]}


  case 'SUPPRIMER_CARTE':
    return {...state,carts:[...state.carts.filter(catr=>parseInt(catr.id) !==parseInt( action.payload))]}

  case 'increment':
    return {...state,carts:[...state.carts.map(catr=>{
      if(catr.id === action.payload){
        return {...catr,quantity:parseInt(catr.quantity)+1}
      }else{
        return catr;
      }
    })]}
    case 'dicrement':
    return {...state,carts:[...state.carts.map(catr=>{
      if(catr.id === action.payload){
        if(catr.quantity===1){
          return {...state.carts.filter(catr=>catr.id!==action.payload)} ;
        }else{
          return {...catr,quantity:parseInt(catr.quantity)-1}
        }
      }else{
        return catr;
      }
      
    } 
    )]}

  case 'AVENT_PROD':
    
    return {...state,produitsAv:[...state.produitsAv,action.payload]}
      
    
  
  default: return state
}
}