import React, { useState } from 'react';
import { useSelector } from 'react-redux';


const ProfielAdmin = () => {
  const [categore, setCategore] = useState('')
  const porduitss = useSelector((state) => state.produis)
  const porduits = categore !== '' ? porduitss.filter(pro => pro.categories === categore) : porduitss

  const userss = useSelector((State) => State.users)
  const users = userss.filter(user => user.etate === 'user')
  const usersA = useSelector((State) => State.users.find(us => us.etate === 'admi'))
  const payerTotal = porduitss.reduce((x, e) => x += parseFloat(e.pay) * parseFloat(e.quantity), 0)

  const ProdAv = useSelector(state => state.produitsAv)

const profit =ProdAv.length;
console.log(ProdAv)
  return (
    <div className=' mt-5 container  '>
      <section className="py-5 text-center ">
        <div className="row py-lg-5 ">
          <div className="col-lg-6 col-md-8 mx-auto">
            <h1 className="fw-light">Bienvenue  <b>{usersA.name} </b>  </h1>
            <p className="lead text-body-secondary"></p>

          </div>

        </div>
      </section>

      <section>
        {/* <h1>{carts[0].id}</h1> */}
        <h1>Statistiques du magasin :</h1>
        <div className=' d-flex justify-content-between  flex-wrap ' >
          <section className='bg-body-secondary p-3 rounded-3 my-2 text-center' style={{ width: '200px' }}>
            <span >
              <p className='m-0 fw-bold '>Nombre de produits:</p><p className='m-0 fs-2 fw-bolder text-info-emphasis'>{porduitss.length}  </p>

            </span>
          </section>
          <section className='bg-body-secondary p-3 rounded-3 my-2 text-center' style={{ width: '200px' }}>
            <span >
              <p className='m-0 fw-bold '>Nombre d'utilisateurs:</p><p className='m-0 fs-2 fw-bolder text-info-emphasis'>{users.length}  </p>

            </span>
          </section>
          <section className='bg-body-secondary p-3 rounded-3 my-2 text-center' style={{ width: '200px' }}>
            <span>
              <p className='m-0 fw-bold'>Payer Total</p><p className='m-0 fs-2 fw-bolder text-info-emphasis'>{payerTotal}</p>
            </span>
          </section>
          <section className='bg-body-secondary p-3 rounded-3 my-2 text-center' style={{ width: '200px' }}>
            <span>
              <p className='m-0 fw-bold'>Nombre de ventes</p><p className='m-0 fs-2 fw-bolder text-info-emphasis'>{profit} </p>
            </span>
          </section>

        </div>
      </section>




      <hr />

      <section>

        <hr />
        <section className='d-flex '>
          <h3>filtre par categorer</h3>
          <select className='form-select-sm text-center text-center w-25 ms-5' onChange={(e) => setCategore(e.target.value)}>
          <option value="">Toute</option>
  <option value="PC">PC</option>
  <option value="VETEMENT">Vetements</option>
  <option value="TABLETTE">Tablettes</option>
  <option value="MOTOR">Moto</option>
  <option value="TELE">Tele</option>
  <option value="VOITURE">Voiture</option>
          </select>
        </section>
        <hr />
        <h2 className='w-100'> Nombre de produits de catégorie : <span className='fw-bold text-warning-emphasis '>{categore}  </span>  :<span className='fw-bolder text-danger-emphasis'>{porduits.length}</span>
        </h2>

      </section>
      <hr />
      <h2>Liste des produits :</h2>

      <table className="table table-striped table-hover">
  <thead>
    <tr>
      <th>Catégorie Produit</th>
      <th>Nom du produit</th>
      <th>Prix (DH)</th>
      <th>Payer/1 (DH)</th>
      <th>Quantité</th>
      <th>Quantité Solde</th>
      <th>Quantité Restante</th>
      <th>Profit (DH)</th>
    </tr>
  </thead>
  <tbody>
    {porduits.map((produit, i) => (
      <tr key={i}>
        <td>{produit.categories}</td>
        <td>{produit.name}</td>
        <td>{produit.price}</td>
        <td>{produit.pay}</td>
        <td>{produit.quantity}</td>
        <td>
          <span className='fw-lighter fs-5 text-success-emphasis'>
            {ProdAv.filter(pa => pa.idProduct === produit.id).reduce((total, a) => total + parseInt(a.qtt), 0)}
          </span>
        </td>
        <td>
          <span className='fw-lighter fs-5 text-danger-emphasis'>
            {produit.quantity - ProdAv.filter(pa => pa.idProduct === produit.id).reduce((total, a) => total + parseInt(a.qtt), 0)}
          </span>
        </td>
        <td>
          <span className='fw-lighter fs-5 text-success-emphasis'>
            {((ProdAv.filter(pa => pa.idProduct === produit.id).reduce((total, a) => total + parseInt(a.qtt), 0))) * parseFloat(produit.price) - parseFloat(produit.pay) * ((ProdAv.filter(pa => pa.idProduct === produit.id).reduce((total, a) => total + parseInt(a.qtt), 0)))}
          </span>
        </td>
      </tr>
    ))}
  </tbody>
</table>

    </div>
  );
}

export default ProfielAdmin;
