import React from 'react';
import { Link } from 'react-router-dom';

const Foter = () => {
  return (
    <footer className="text-body-secondary  bottom-0 bg-opacity-100 bg-body-tertiary p-2 rounded-top-4 mt-3">
  <div className="container">
    
    <p className="mb-1">TINITI STOR &copy; Merci pour votre visite</p>
    <p className="mb-0">Qui sommes-nous ? <Link to="/"><i className='bi bi-github'></i> </Link> or read our <Link to="/"><i className='bi bi-linkedin'></i></Link>.
    <span className='ms-3'>
    <Link to="/"><i className='bi bi-telephone'></i></Link><Link to="/"><i className='bi bi-whatsapp'></i></Link>
  </span>
    
    </p>
  </div>
</footer>
  );
}

export default Foter;
