import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { modifierProduitAction } from '../redux/actions';
import Swal from 'sweetalert2';

const UpdateProduit = () => {
  const nav=useNavigate()
  const { id } = useParams()
  const produis=useSelector(state=>state.produis)
  const produit=produis.find(prod=>parseInt(prod.id)===parseInt(id))
  const [name, setName] = useState(produit.name)
  const [prix, setPrix] = useState(produit.price)
  const [pay, setPay] = useState(produit.pay)
  const [qtt, setQtt] = useState(produit.quantity)
  const [descriptions, setDescriptions] = useState(produit.description)
  const [detialles, setDetialles] = useState(produit.detialles)
  const photo = produit.photo;
  const categories=produit.categories 

  const dispatch=useDispatch()
  function modifierProduit(){

    if(name.trim()===''|| prix===0|| pay===0 || descriptions.trim()===''||detialles.trim()===''||qtt===0){
      Swal.fire({
        width: '300px',
  
        position: "top",
        icon: "error",
        title: "le produit ne pas ajoute",
        showConfirmButton: false,
        timer: 3000
      });
    } else{
    dispatch(modifierProduitAction(id,name,prix,pay,qtt,categories,photo,descriptions,detialles))
    Swal.fire({
      width: '300px',
  
      position: "top",
      icon: "success",
      title: "le produit bien modifie",
      showConfirmButton: false,
      timer: 3000
    });
    nav(`/homme/detailes/${id}`)
  }
  }
  return (
    <div className="container  px-4 d-flex flex-column">
      <div className=" g-lg-5 py-1">
        <section className="py-2 text-center ">
          <div className="row py-lg-5">
            <div className="col-lg-6 col-md-8 mx-auto">
              <h1 className="fw-light">Modifier produit  <b>{} </b>  </h1>
              <p className="lead text-body-secondary"></p>

            </div>

          </div>
        </section>


        <div className="col-md-10 mx-auto ">
          <section className="p-4 p-md-5 border rounded-3 bg-body-tertiary">
            <div className=" mb-3 ">
              <div className='form-floating'>
                <input type="text" className="form-control" id="floatingInput" onChange={(e)=>setName(e.target.value)} value={name} />
                 <label for="floatingInput">Name</label>
              </div>

            </div>
            <div className="d-flex justify-content-between">
              <div className="form-floating mb-3">
                <input type="number" className="form-control" id="floatingInput" onChange={(e)=>setPrix(e.target.value)}  value={prix}/>
                 <label for="floatingInput">prix</label>
              </div>
              <div className="form-floating mb-3">
                <input type="number" className="form-control" id="floatingInput" onChange={(e)=>setPay(e.target.value)}  value={pay}/>
                 <label for="floatingInput">Pay</label>
              </div>
              <div className="form-floating mb-3">
                <input type="number" className="form-control" id="floatingInput" onChange={(e)=>setQtt(e.target.value)}  value={qtt}/>
                 <label for="floatingInput">Quantiter</label>
              </div>

            </div>
            <div className="form-floating mb-3">
              <textarea type="text" className="form-control" id="floatingInput"  onChange={(e)=>setDescriptions(e.target.value)} value={descriptions} />
              <label for="floatingInput">description</label>
            </div>
            <div className="form-floating mb-3">
              <textarea type="text" className="form-control" id="floatingInput" onChange={(e)=>setDetialles(e.target.value)} value={detialles} /> 
              <label for="floatingInput">detialles</label>
            </div>

            <button className="w-100 btn btn-lg btn-primary" onClick={modifierProduit}>Modifie</button>
            <Link to={`/homme/detailes/${id}`} class="w-100 btn btn-lg btn-outline-secondary mt-2 ">Annulle</Link>
            <hr className="my-4" />
            <p className="text-body-secondary text-center">pour modifie le produits P1 </p>


          </section>

        </div>

      </div>
    </div>

  );
}

export default UpdateProduit;
