import React, { useState } from 'react';

import { useSelector,useDispatch } from 'react-redux';

import { Link } from 'react-router-dom';


import { ajouterProduitAction } from '../redux/actions';
import Swal from 'sweetalert2';
const AjouterProduit = () => {
  const [nom,setName]=useState('')
  const [category,setCategory]=useState('')
  const [prix,setPrix]=useState()
  const [pay,setPay]=useState()
  const [descriptions,setDescription]=useState('')
  const [detialles,setDetialles]=useState('')
  const [qtt,setQtt]=useState()

  const produits=useSelector(state=>state.produis)
const id=produits.length+1
  const photo='https://res-console.cloudinary.com/dxlwebrq2/media_explorer_thumbnails/2fa75768810ff4ae73d31ad68e779eb7/detailed'
  
  const dispatch=useDispatch()
  function handlAjoutProduits(){

    if(nom.trim()==''|| prix==0,category.trim()==''|| pay==0 || descriptions.trim()==''||detialles.trim()==''){
      Swal.fire({
        width: '300px',
  
        position: "top",
        icon: "error",
        title: "le produit ne pas ajoute",
        showConfirmButton: false,
        timer: 3000
      });
    }
    else {
    dispatch(ajouterProduitAction(id,nom,prix,pay,qtt,category,photo,descriptions,detialles))
    Swal.fire({
      width: '300px',

      position: "top",
      icon: "success",
      title: "le produit bien ajoute",
      showConfirmButton: false,
      timer: 3000
    });
    setName('')
    setCategory('')
    setPrix('')
    setPay('')
    setQtt('')
    setDescription('')
    setDetialles('')
    
  }
}

  return (
    <div className="container px-md-5 py-2 d-flex flex-column">
    <div className=" g-lg-5 py-3">
    <section className=" text-center ">
    <div className="row py-lg-5 ">
      <div className="col-lg-6 col-md-8 mx-auto">
        
        <h1 className="fw-light">Ajouter le produit de numero : <b> </b> {id} </h1>
        <p className="lead text-body-secondary"></p>
        
      </div>
    
    </div>
  </section>

      <div className="col-md-10 mx-auto ">
        <section className="p-4 p-md-5 border rounded-3 bg-body-tertiary">
          <div className=" mb-3 ">
          <div className='form-floating me-4 '>
          <input type="text" className="form-control" id="floatingInput" value={nom} onChange={(e)=>setName(e.target.value)} />
            <label for="floatingInput">Name</label>
            </div>
          
          </div>
          <div className="d-flex justify-content-between">
          <div className='form-floating'>
            <select type="text" className="form-control" id="floatingInput" value={category} onChange={(e)=>setCategory(e.target.value)} required>
              <option value="">choiser category</option>
              <option value="PC">PC</option>
              <option value="VETEMENT">Vetements</option>
              <option value="TABLETTE">Tablettes </option>
              <option value="MOTOR">Motoure </option>
              <option value="TELE">Tele</option>
              <option value="VOITEUR">Voiteur </option>
            </select>
            <label for="floatingInput">Category</label>
          </div>
          <div className="form-floating mb-3">
            <input type="number" className="form-control" id="floatingInput"  value={prix} onChange={(e)=>setPrix(e.target.value)} required/>
            <label for="floatingInput">prix</label>
          </div>
          <div className="form-floating mb-3">
            <input type="number" className="form-control" id="floatingInput"  value={pay} onChange={(e)=>setPay(e.target.value)} required/>
            <label for="floatingInput">Pay</label>
          </div>
          <div className="form-floating mb-3">
            <input type="number" className="form-control" id="floatingInput"  value={qtt} onChange={(e)=>setQtt(e.target.value)} required/>
            <label for="floatingInput">Quantiter</label>
          </div>

          </div>
          <div className="form-floating mb-3">
            <textarea type="text" className="form-control" id="floatingInput"  value={descriptions} onChange={(e)=>setDescription(e.target.value)} required />
            <label for="floatingInput">description</label>
          </div>
          <div className="form-floating mb-3">
            <textarea type="text" className="form-control" id="floatingInput"  value={detialles} onChange={(e)=>setDetialles(e.target.value)}  required='true'/>
            <label for="floatingInput">detialles</label>
          </div>
          
          <button className="w-100 btn btn-lg btn-primary" onClick={handlAjoutProduits}>Ajouter</button>
          <Link to={`/homme`} className="w-100 btn btn-lg btn-outline-secondary mt-2 ">Annulle</Link>
          <hr className="my-4"/>
          <p className="text-body-secondary text-center">pour ajoutr des produits </p>
          

        </section>
        
      </div>
    </div>
  </div>
  );
}

export default AjouterProduit;
