export const ajouterProduitAction=(id,name,price,pay,quantity,categories,photo,description,detialles)=>{
  return{type:'ADD_PRODUIT',payload:{id:id,name:name,price:price,pay:pay,quantity:quantity,categories:categories,photo:photo,description:description,detialles:detialles}}
}




export const modifierProduitAction=(id,name,price,pay,quantity,categories,photo,description,detialles)=>{
  return {type:'UPDATE_PRODUIT',payload:{id:id,name:name,price:price,pay:pay,quantity:quantity,categories:categories,photo:photo,description:description,detialles:detialles}}
}


export const supprimerProduitAction=(id)=>{
  return {type:"DELETE_PRODUIT" ,payload:id}
}


export const signUpAction=(id,name,username,password,etate)=>{
  return {type:'SIGN_UP',payload:{id:id,name:name,username:username,password:password,etate:etate}};
}




export const AjouterProduitClientsCartAction=(id,name,price,quantity,categories,photo,idC)=>{
return {type:'ADD_CART_CLIENT' ,payload:{id:id,name:name,price:price,quantity:quantity,categories:categories,photo:photo,idC:idC}};

}
export const plusQuantiProduitClientsCartAction=(id,name,price,quantity,categories,photo,idC)=>{
  return {type:'PLUS_QUNT_PROD' ,payload:{id:id,name:name,price:price,quantity:quantity,categories:categories,photo:photo,idC:idC}};
}

export const suprimerCarte=(id)=>{
  return {type: 'SUPPRIMER_CARTE',payload:id}
}

export const ProduitAventActions=(id,idprod,qtt,idUser,total)=>{
  return {type:'AVENT_PROD' ,payload:{id:id,idProduct:idprod,qtt:qtt,idUser:idUser,total:total}}
}

export const IncrementQt =(idp)=>{
  return {type: 'increment',payload:idp}
}

export const DicrementQt =(idp)=>{
  return {type: 'dicrement',payload:idp}
}