import React, { useEffect, useState } from 'react';

import Header from './componentBoot/header';
import Homme from './pages/Homme';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Ditailes from './pages/ditailes';

import Login from './pages/login';
import SignUp from './pages/SignUp';
import UpdateProduit from './pages/updateProduit';
import { useSelector } from 'react-redux';

import ProfielAdmin from './pages/profielAdmin';
import CartClients from './pages/cartClients';
import AjouterProduit from './pages/ajouterProduit';
import Carousel from './componentBoot/carousel';
// import Data from './redux/data';
const AppBootstrap = () => {
  const [etate, setEtate] = useState('')
  const [quantity, setQuantity] = useState(1)
  const [id, setId] = useState()



  const users = useSelector((state) => state.users)
  const user = etate !== '' ? users.find(user => user.id == id) : { id: 0 }
  const Swal = require('sweetalert2')
  useEffect(() => console.log('f'), [quantity])
  return (
    <div className=' '>

      <BrowserRouter>
        <Header etate={etate} id={user.id} setEtate={setEtate} />
        {/* <h1>{ettat.id}</h1> */}

        <Routes>

          {etate.trim() == '' ? <><Route path='/' element={<Carousel />} />
            <Route path='/signUp' element={<SignUp setId={setId} />} />
            <Route path='/logIn' element={<Login setId={setId} setEtate={setEtate} />} />
          </> : <>

            {etate=='admi'?<Route path='/homme' element={<>
              <ProfielAdmin etate={etate} id={user.id} quantity={quantity} setQuantity={setQuantity} /></>} />:<Route path='/homme' element={<>
              <Homme etate={etate} id={user.id} quantity={quantity} setQuantity={setQuantity} /></>} />}
            <Route path='/homme/detailes/:id' element={<Ditailes etate={etate} />} />
            <Route path='/login' element={<Login />} />
            <Route path='/ajouteProduit' element={<AjouterProduit />} />

            <Route path='/homme/detailes/:id/updateProduit/:id' element={<UpdateProduit />} />
            {etate == 'admi' ? <Route path='/listProduits' element={<Homme etate={etate} id={user.id} quantity={quantity} setQuantity={setQuantity} />} /> : <Route path='/profiel/:id' element={<CartClients id={id} />} />}
            <Route path='/storClient/:id' element={<CartClients id={id} />} />
          </>}

        </Routes>


      </BrowserRouter>


    </div>
  );
}

export default AppBootstrap;
