import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Foter from './foter';
import MainCarousel from './mainCarousel';

class Carousel extends Component {
  listeCategories = [{ img: 'https://res.cloudinary.com/dxlwebrq2/image/upload/v1706178587/pictuers/uzty0hdv1u2rzixcwos6.jpg', title: 'Ordinateurs' }, { img: 'https://res.cloudinary.com/dxlwebrq2/image/upload/v1705875776/pictuers/jw8ex2ac72kcz45wgxdh.jpg', title: 'Vetements' }, { img: 'https://res.cloudinary.com/dxlwebrq2/image/upload/v1706178474/pictuers/m5d4qpx2h3f3tqzknant.jpg', title: 'Tablettes' }, { img: 'https://res.cloudinary.com/dxlwebrq2/image/upload/v1706178367/pictuers/j87duizqvtzkxcsje9wu.jpg', title: 'Moto ... ...' }, { img: 'https://res.cloudinary.com/dxlwebrq2/image/upload/v1706191548/pictuers/sfcn1yhrvugclhjlhwq8.jpg', title: 'Telephones' }, { img: 'https://res.cloudinary.com/dxlwebrq2/image/upload/v1706191563/pictuers/uxwfnivrqvv6j9gjiiw3.jpg', title: 'Voiture ...' }]
  render() {
    return (
      <>
        <div id="myCarousel" className="carousel slide" data-bs-ride="carousel">
          <div className="carousel-indicators">
            {this.listeCategories.map((i, o) => <button key={o} type="button" data-bs-target="#myCarousel" data-bs-slide-to={o} className="active"
              aria-current="true"
              aria-label={`Slide ${o + 1}`}>

            </button>)}


          </div>




          <div className="carousel-inner  " style={{ height: "500px", margin: 'auto', display: "block" }}>
            {this.listeCategories.map((cat, i) =>
              <div key={i} className={`carousel-item ${i == 1 ? 'active' : ''}`}>
                <img src={`${cat.img}`} alt="" className="bd-placeholder-img  rounded-3" width="100%"
                  style={{ height: "500px", margin: 'auto', display: "block" }}
                  xmlns="./pictures/C1.jpg" aria-hidden="true"
                  preserveAspectRatio="xMidYMid slice" focusable="false" />
                <div className="container">
                  <div className="carousel-caption text-start ">
                    <h1 className='text-bg-light p-2 opacity-75 rounded-3  d-md-inline-block'>Categories : {cat.title}</h1>
                    <h4 className="opacity-50 text-bg-light p-2 rounded-3 d-md-inline-block">
                      Produits disponibles dans la catégorie des {cat.title}
                    </h4>
                    <p>
                      <Link className="btn btn-outline-light" to="/logIn">
                        Visiter
                      </Link>
                    </p>

                  </div>
                </div>
              </div>

            )}


          </div>


          <button className="carousel-control-prev" type="button" data-bs-target="#myCarousel" data-bs-slide="prev">
            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
            <span className="visually-hidden">Previous</span>
          </button>
          <button className="carousel-control-next" type="button" data-bs-target="#myCarousel" data-bs-slide="next">
            <span className="carousel-control-next-icon" aria-hidden="true"></span>
            <span className="visually-hidden">Next</span>
          </button>


        </div>
        <div className="album py-5 bg-body-tertiary">
          <div className="container-fluid">




            <h2 className='text-center'> Produits ...</h2>
            <MainCarousel />
          </div>
        </div>
        <Foter />
      </>
    );
  }
}

export default Carousel;
