import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { signUpAction } from '../redux/actions';
import { Link, useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

const SignUp = (props) => {
  const {setId}=props
const [name,setName]=useState('')
const [login,setLogin]=useState('')
const [password,setPassword]=useState('')
const [passCon,setPassCon]=useState('')
const etate='user'
const id=useSelector((state)=>state.users.length)+1
const dispatch=useDispatch()
const nav=useNavigate()

const users=useSelector((state)=>state.users)
const user=users.find(user=>user.username==login)
function handelSignUp(){
if(name.trim()=='' || login.trim()=='' || password.trim()=='' || passCon.trim()==''){
  Swal.fire({
    width:'300px',
    
    position: "top",
    icon: "warning",
    title: "Tout les  champs sont obligatoires",
    showConfirmButton: false,
    timer: 3000
  });
}
else if(user){
  Swal.fire({
    width:'300px',
    
    position: "top",
    icon: "warning",
    title: "User ne existe pas",
    showConfirmButton: false,
    timer: 3000
  });
}else if(passCon===password){
dispatch(signUpAction(id,name,login,password,etate))
setId(id)
nav('/logIn')}
else {
  Swal.fire({
    width:'300px',
    
    position: "top",
    icon: "warning",
    title: "confirme password",
    showConfirmButton: false,
    timer: 3000
  });}
}

  return (
  
    <div className="container col-xl-10 col-xxl-8 px-4 py-5">
    <div className="row align-items-center g-lg-5 py-5">
    <div className="col-lg-7 text-center text-lg-start">
        <h1 className="display-4 fw-bold lh-1 text-body-emphasis mb-3">TAINIT STORE</h1>
        <p className="col-lg-10 fs-4">Vous trouverez ici tout ce que ous cherchez . Nous avons ici de nombreux types de produis . <h3>bienvenue</h3></p>
      </div>
      <div className="col-md-10 mx-auto col-lg-5">
        <section className="p-4 p-md-5 border rounded-3 bg-body-tertiary">
          <div className="form-floating mb-3">
            <input type="text"  className="form-control" id="floatingInput" placeholder="name@example.com" onChange={(e)=>setName(e.target.value)}/>
            <label for="floatingInput">Name</label>
          </div>
          <div className="form-floating mb-3">
            <input type="text" className="form-control" id="floatingInput" placeholder="name@example.com" onChange={(e)=>setLogin(e.target.value)}/>
            <label for="floatingInput">login</label>
          </div>
          <div className="form-floating mb-3">
            <input type="password" className="form-control" id="floatingPassword" placeholder="Password" onChange={(e)=>setPassword(e.target.value)}/>
            <label for="floatingPassword">Password</label>
          </div>
          <div className="form-floating mb-3">
            <input type="password" className="form-control" id="floatingPassword" placeholder="Password" onChange={(e)=>setPassCon(e.target.value)}/>
            <label for="floatingPassword">Confige Pessword</label>
          </div>
          
          <button className="w-100 btn btn-lg btn-outline-warning" onClick={handelSignUp}>Sign up</button>
          <h6 className='text-center mt-2'><Link to='/logIn' className='text-success'>log-In</Link></h6>
          <hr className="my-4"/>
          <small className="text-body-secondary">By clicking Sign up, you agree to the terms of use.</small>
        </section>

      
      
      </div>
    </div>
  </div>
  );
}

export default SignUp;
