import React from 'react';
import Main from '../componentBoot/main';

const Homme = (props) => {
  
const {etate,id,quantity,setQuantity}=props;
  return (
    <div>
    
      {etate.trim()!==''?<Main etate={etate} id={id} quantity={quantity} setQuantity={setQuantity}/>:alert('lllll')}
      
    </div>
  );
}

export default Homme;
