import React from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { DicrementQt, IncrementQt, ProduitAventActions, suprimerCarte } from '../redux/actions';
import Swal from 'sweetalert2';

const CartClients = (props) => {
  const dispatch = useDispatch()
  const { id } = props
  const user = useSelector((state) => state.users.find(u => u.id == id))

  const carts = useSelector((state) => state.carts.filter(car => car.idC == user.id))
  const total = carts.reduce((total, xx) => total += parseFloat(xx.price) * parseFloat(xx.quantity), 0)

  const idAv = useSelector(state => state.produitsAv.length) + 1

  function handlDeletProdCrte(idCa) {
    dispatch(suprimerCarte(idCa))
    Swal.fire({
      width: '300px',

      position: "top",
      icon: "success",
      title: "Supprimer avec succes",
      showConfirmButton: false,
      timer: 3000
    });
  }


  function handleAventProd(id, idCa, qtt, idUse, total) {

    let x=parseInt(prompt('RIB: '));
    let y=parseInt(prompt('Code: ' ));
    if(!isNaN(x) && !isNaN(y)){

    dispatch(ProduitAventActions(id, idCa, qtt, idUse, total))
  
    dispatch(suprimerCarte(idCa))
    Swal.fire({
      width: '300px',

      position: "top",
      icon: "success",
      title: "Achete avec succes",
      showConfirmButton: false,
      timer: 3000
    });
  }


  }

  return (
    <div className='container-fluid'>
      <div className="row py-lg-5 ">
        <div className="col-lg-6 col-md-8 mx-auto">
          <h1 className="fw-light">Bienvenue  <b>{user.name} </b> </h1>
          <p className="lead text-body-secondary"></p>

        </div>
      </div>
      <h1>
        liste des produits que vous avez achetes :
      </h1>

      <table className="table table-group-divider table-hover table-bordered">
        <thead>
          <tr>
            <th> </th>

            <th>Nom produit</th>

            <th>Quantite</th>
            <th>Prix</th>
            <th>Prix total</th>

            <th> </th>
            <th> </th>


          </tr>
        </thead>
        <tbody>
          {carts.map((cart, i) => <tr key={i}>
            <td>{i + 1}</td>
            <td>{cart.name}</td>
            <td><span onClick={() => dispatch(DicrementQt(cart.id))} style={{ cursor: 'pointer', marginRight: '6px' }}> <i className=" bi bi-caret-down text-danger"></i></span>{cart.quantity} <span onClick={() => dispatch(IncrementQt(cart.id))} style={{ cursor: 'pointer', marginLeft: '6px' }}><i className=" bi bi-caret-up text-success"></i></span></td>
            <td>{cart.price}</td>
            <td>{parseFloat(cart.price) * parseFloat(cart.quantity)}</td>
            <td onClick={() => handlDeletProdCrte(cart.id)} style={{ cursor: 'pointer' }}> <i className="bi bi-cart-dash text-danger"></i></td>
            <td onClick={() => handleAventProd(idAv, cart.id, cart.quantity, cart.idC, parseFloat(cart.price) * parseFloat(cart.quantity))} style={{ cursor: 'pointer' }}> <i className="bi bi-cart-plus text-success"></i></td>

          </tr>)}
          <tr>
            <th style={{ textAlign: 'center' }} colSpan={2}>Total </th> <td className='fw-bolder text-warning-emphasis' style={{ textAlign: 'center' }} colSpan={5}> <span className='bg-body-tertiary p-1 rounded-5'>{total} DH</span></td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}

export default CartClients;
