import React from 'react';
import { Link, useParams } from 'react-router-dom';
import './heroes.css'
const Heroes = (props) => {

  const { product, etate } = props;

  return (
    <div className="container col-xxl-8 px-4 py-5">
      <div className="row flex-lg-row-reverse align-items-center g-5 py-5">
        <div className="col-10 col-sm-8 col-lg-6">
          <section className='p-2 border border-dark-subtle rounded rounded-3 '>
            <img src={`${product.photo}`} className="w-100 rounded rounded-3" alt={`photo produit: ${product.name}`} height='400' />
          </section>
        </div>
        <div className="col-lg-6">
          <h1 className="display-5 fw-bold text-body-emphasis lh-1 mb-3">{product.name}<span className='ms-5 fs-2'>{product.price} DH</span> </h1>
          <p>{product.description}</p>
          <h5 className="lead">{product.detialles}</h5>
          <div className="d-grid gap-2 d-md-flex justify-content-md-start mt-5">

            {etate == 'admi' ? <Link
  to={`/homme/detailes/${product.id}/updateProduit/${product.id}`}
  className="btn btn-primary btn-lg px-4 me-md-2 w-50 m-auto"
>
  Modifier
</Link>
 : ''}
          

            <Link to="/listProduits" className="btn btn-outline-secondary btn-lg px-4 w-50 m-auto">
  Annuler
</Link>

          </div>
        </div>
      </div>
    </div>
  );
}

export default Heroes;
