import { Link } from 'react-router-dom';
import './headers.css'
import './product.css'
import { useSelector } from 'react-redux';




const Header = (props) => {
  const {etate,id,setEtate}=props;
const users=useSelector((state)=>state.users)

const user=users.find(user=>user.id == id)
const carts=useSelector((state)=>state.carts)
  return (
<>
    

<nav className="navbar navbar-expand-md opacity-100 bg-body-tertiary fixed-top ">
  <div className="container-fluid">
  <Link to={etate.trim()!==''?'/homme':'/'} className="d-flex ms-md-5 me-sm-5 mb-3 mb-md-0 me-md-auto link-body-emphasis text-decoration-none">
        <img src='https://res.cloudinary.com/dxlwebrq2/image/upload/v1706705234/profile/txmiitied77yseyiw9bh.jpg' className=" me-2 rounded-4 me-md-3" width="40" height="35"/>
        <span className="fs-4">TAINIT STOR</span>
      
      
      </Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarCollapse" aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse " id="navbarCollapse">
      <ul className="navbar-nav me-auto mb-2 mb-md-0">
        <li className="nav-item">
          <Link to={etate.trim()!==''?'/homme':'/'} className="nav-link ms-3" aria-current="page" >Home</Link>
        </li>
        {etate=='admi'?<><li className="nav-item">
          <Link class="nav-link" to='/ajouteProduit'>Ajouter Produit</Link>
        </li><li className="nav-item">
          <Link class="nav-link" to='/listProduits'>Liste produis</Link>
        </li></>:<>{id!==0?<Link className="nav-link ms-3" to={`/storClient/${id}`}><i className='bi bi-cart' title='my Cart' ></i> 
        <sup className='text-success fw-bold fw-semibold'>{carts!=undefined?carts.length:0}</sup>
        </Link>:''}</>}
      
      </ul>
      {etate.trim()!==''?<><div className="col-md-3 text-end me-2 ms-sm-5 ps-sm-5">
        <Link to='/'>
          <button onClick={()=>setEtate('')}  className="btn btn-outline-danger me-2 ms-sm-2">Log Out</button></Link>
        {etate=='admi'?'':<Link to={`/profiel/${user.id}`}><button type="button" className="btn btn-secondary">{user.name}</button></Link> }
          
      </div></>:
      <div className="col-md-3 text-end me-2 ms-sm-5 ps-sm-5">
        
        <Link to='/signUp'><button type="button" className="btn text-warning">Sign-up</button></Link>
        <Link to='/logIn'><button type="button" className="btn text-success">Log In</button></Link>
        
      </div>}
    </div>
    
  </div>
</nav>
    <section style={{height:'70px'}}></section>
    </>
  );
}

export default Header;
