import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from
  'react-redux';
import Swal from 'sweetalert2';
import { supprimerProduitAction, AjouterProduitClientsCartAction, plusQuantiProduitClientsCartAction, ProduitAventActions } from '../redux/actions';
import Foter from './foter';
const Main = (props) => {
  const { etate, id, quantity } = props;

  const [categories, setCategory] = useState('')




  const dispatch = useDispatch()
  const produis1 = useSelector((state) => state.produis)
  const cateFilter = produis1.filter(prod => prod.categories == categories)
  const produis = categories == '' ? produis1 : cateFilter

  const carts = useSelector((state) => state.carts)


  function handelSupprimer(id) {

    dispatch(supprimerProduitAction(id))
    Swal.fire({
      width: '300px',

      position: "top",
      icon: "success",
      title: "le produit bien supprimer",
      showConfirmButton: false,
      timer: 3000
    });

  }




  function handleAjouterProduitClientsCart(idPro, name, price, categories, photo) {
    const cart = carts.find(c => c.id == idPro);
    if (cart) {


      let quantity = parseInt(cart.quantity) + 1;


      dispatch(plusQuantiProduitClientsCartAction(idPro, name, price, quantity, categories, photo, id))
      Swal.fire({
        width: '300px',

        position: "top",
        icon: "success",
        title: "bien ajoute la quantity",
        showConfirmButton: false,
        timer: 3000
      });

    }
    else {
      let quantitys = quantity;
      dispatch(AjouterProduitClientsCartAction(idPro, name, price, quantitys, categories, photo, id))

      Swal.fire({
        width: '300px',

        position: "top",
        icon: "success",
        title: "le product bien ajoute dans carte",
        showConfirmButton: false,
        timer: 3000
      });
    }
  }


  const idAv = useSelector(state => state.produitsAv.length) + 1


  function handleAventProds(id, idpr, qtt, idu) {
    let x = parseInt(prompt('RIB: '));
    let y = parseInt(prompt('Code: '));
    if (!isNaN(x) && !isNaN(y)) {
      dispatch(ProduitAventActions(id, idpr, qtt, 1, idu))

      Swal.fire({
        width: '300px',

        position: "top",
        icon: "success",
        title: "Achete avec succes",
        showConfirmButton: false,
        timer: 3000
      });
    }

    else {
      Swal.fire({
        width: '300px',

        position: "top",
        icon: "warning",
        title: "pas achete avec succes",
        showConfirmButton: false,
        timer: 3000
      });
    }


  }

  return (
    <>

      <main>

        <section className="py-5 text-center container">
          <div className="row py-lg-5 ">
            <div className="col-lg-6 col-md-8 mx-auto">
              <h1 className="fw-light">TAINIT STORE</h1>
              
              <p className="lead text-body-secondary">Vous trouverez ici tout ce que vous cherchez. Nous avons ici de nombreux types de produits. <h3>Bienvenue</h3></p>



              <p>
                
                <a href="#" className="btn btn-primary my-2 m-2">Linkedin <i className=" bi bi-linkedin "></i></a>
                <button href="#" className="btn btn-secondary my-2">GitHub <i className=" bi bi-github "></i></button>
              </p>
            </div>
          </div>
        </section>

        <div className="album py-5 bg-body-tertiary">
          <div className="container-fluid">
            <section className='d-flex justify-content-between'>
              <h1></h1>
              <section className='d-flex flex-wrap justify-content-between bg-body p-1 rounded-3 align-items-center'>
        <span className='  mx-1 px-2  fs-4 fw-bold'>categores: </span>
                <span style={{cursor: 'pointer'}} className='bi bi-house  mx-1 px-2 text-info-emphasis fs-4 fw-bold ' onClick={()=>setCategory('')}></span>
                <span style={{cursor: 'pointer'}} className='bi bi-laptop mx-1 px-2 text-info-emphasis fs-4 fw-bold'  onClick={()=>setCategory('PC')}></span>
                <span style={{cursor: 'pointer'}} className='bi bi-handbag  mx-1 px-2 text-info-emphasis fs-4 fw-bold' onClick={()=>setCategory('VETEMENT')}></span>
                <span style={{cursor: 'pointer'}} className='bi bi-phone-landscape mx-1 px-2 text-info-emphasis fs-4 fw-bold' onClick={()=>setCategory('TABLETTE')}></span>
                <span style={{cursor: 'pointer'}} className='bi bi-bicycle mx-1 px-2 text-info-emphasis fs-4 fw-bold'  onClick={()=>setCategory('MOTOR')}></span>
                <span style={{cursor: 'pointer'}} className='bi bi-phone mx-1 px-2 text-info-emphasis fs-4 fw-bold'  onClick={()=>setCategory('TELE')}></span>
                <span style={{cursor: 'pointer'}} className='bi bi-car-front mx-1 px-2 text-info-emphasis fs-4 fw-bold'  onClick={()=>setCategory('VOITEUR')}></span>
                
                </section>
            </section>


          
            <h2>Les produits :</h2>
            <div className="row row-cols-1 row-cols-sm-2 row-cols-md-4 g-3 ">
              {
                produis.map((produi) => <div key={produi.id} className="col " >
                  <div className="card shadow-sm hov ">
                    <section className='p-2 hov1'>
                      <Link to={`/homme/detailes/${produi.id}`}><img src={`${produi.photo}`} className="bd-placeholder-img card-img-top" width="100%" height="180" /></Link>
                    </section>

                    <div className="card-body">
                      <h5 className='d-flex justify-content-between'><span>{produi.name} </span><span>{produi.price} DH</span></h5>
                      <p className="card-text">{produi.description}</p>
                      <div className="d-flex justify-content-between align-items-center">
                        <div className="btn-group">

                          {etate == 'admi' ? <> <button type="button" className="btn btn-sm btn-outline-danger" onClick={() => handelSupprimer(produi.id)}> <i className='bi bi-basket'></i> </button><Link to={`/homme/detailes/${produi.id}/updateProduit/${produi.id}`} className="btn btn-sm btn-outline-info"> <i className='bi bi-pencil'></i></Link></> : <>
                            <button onClick={() => {

                              handleAjouterProduitClientsCart(produi.id, produi.name, produi.price, produi.categories, produi.photo)
                            }} type="button" className="btn btn-sm btn-outline-success " > {' '}<i class=" bi bi-cart-plus "></i></button>
                            <button type="button" className="btn btn-sm btn-outline-success" onClick={() => handleAventProds(idAv, produi.id, 1, id)}><i className=" bi bi-cart-plus-fill"></i></button>
                          </>}
                        </div>
                        <small className="text-body-secondary">NiTi Tayniti</small>
                      </div>
                    </div>
                  </div>
                </div>)
              }




            </div>
          </div>
        </div>


      </main>

      <Foter />
    </>
  );
}

export default Main;
